@import url(https://fonts.googleapis.com/css?family=Karla|Srisakdi);
@import url(https://fonts.googleapis.com/css?family=Libre+Barcode+39+Text);
@import url(https://fonts.googleapis.com/css?family=Abel);

body {
  margin: 0px;
  padding: 0px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/static/media/trianglify.581612da.svg);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center; }

.mainContent {
  display: -webkit-flex;
  display: flex; }
  .mainContent section {
    width: 20%;
    margin: 14px 0px 0px 18px; }
  .mainContent main {
    width: 80%;
    margin-left: 4%;
    text-align: left;
    font-family: "Srisakdi", cursive; }

header {
  width: 100vw;
  height: 18vh;
  box-sizing: border-box;
  margin: 0px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; }

header .heading {
  color: #01170f;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-family: "Libre Barcode 39 Text", cursive;
  height: 75%; }
  header .heading h1 {
    font-weight: 100;
    font-size: 4em; }

header .icons {
  height: 25%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around; }
  header .icons div {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 20%; }

.iconHref {
  color: inherit; }

.leftBanner, .techStackHeader {
  padding: 10px;
  width: 100%;
  border-radius: 1pc; }

.leftBanner ul {
  margin: 0;
  box-sizing: border-box;
  padding: 0;
  text-align: left; }
  .leftBanner ul li {
    margin-bottom: 15px;
    font-family: "Karla", sans-serif; }

.techStackHeader {
  font-size: 2em;
  opacity: 1;
  -webkit-transform: none;
          transform: none;
  border-radius: 20px;
  margin-bottom: 4px;
  padding: 10px;
  width: 100%;
  font-family: "Srisakdi", cursive;
  box-shadow: -3px 3px 2px 0px; }

.techLi {
  list-style: none; }

.responsibilites {
  list-style: none;
  font-family: 'Abel', sans-serif;
  text-align: left;
  -webkit-transform: rotateX(-2deg) rotateY(-9deg);
          transform: rotateX(-2deg) rotateY(-9deg); }

.responsibilites li {
  padding-bottom: 3px;
  font-size: 1.2em;
  text-shadow: 9px 9px 4px #0606064f; }

.details h3 {
  font-size: x-large; }

.details {
  -webkit-perspective: 620px;
          perspective: 620px; }

.errorPageBox {
  font-size: 1.8em;
  border-radius: 20px;
  border-style: solid;
  border-color: darkgray;
  box-shadow: 7px 8px 19px 0px;
  padding: 5%;
  margin: 0 auto;
  width: 84%; }

.errorPageGear {
  margin: 33.5%; }

